'use strict';
/**
 * This API is deprecated and should not be relied upon.
 */
var SafDetails = {
    buildSection: function($items) {
        var id, title;
        var $children, $item;
        var $listItem,
            $link,
            $list = $('<ul>');

        $items.each(function(index, item) {
            $item = $(item);

            id = $item.attr('id');
            title = $item.attr('title');

            if (id !== null && id !== undefined) {
                $listItem = $('<li>');

                $link = $('<a>', {
                    class: 'toc-link',
                    text: title || id,
                    title: title || id,
                    href: '#' + id,
                });

                $listItem.append($link);
                $list.append($listItem);

                //$children = $item.find('.details-section-child');
                $children = $item.find('.detail-section');

                if ($children.length > 0) {
                    $listItem.append(SafDetails.buildSection($children));
                }
            }
        });

        return $list;
    },

    initTOC: function() {
        var $menu = $('#toc-menu');
        var $main = $('#main');
        //		var $main = $('#main-doc');
        //var $sections = $main.find('.details-section').not('.details-section-child');
        var $sections = $main.find('.detail-section').not('.nested-section  .detail-section');
        var $section, id, $item, $link;
        var items = [];

        var $list = $('<ul>');

        $menu.append(SafDetails.buildSection($sections));

        $(document).on('click', '.toc-toggle', function(e) {
            var $this = $(this);
            var $container_id = $this.attr('rel');
            var $container = $('#' + $container_id);
            $this
                .children('i')
                .toggleClass('fa-plus')
                .toggleClass('fa-minus');
            $container.toggleClass('toc-collapsed');
        });

        $(document).on('click', '.toc-link', function(e) {
            e.preventDefault();
            $('.toc-toggle').click();
            var $this = $(this);
            var target_id = $this.attr('href').replace('#', '');
            var $target_section = $("[id='" + target_id + "']");

            $('html,body').animate({
                scrollTop: $target_section.offset().top - 100,
            });
        });
    },

    goBack: function() {
        window.location = swa.server + searchPortal.servlet;
    },
};

$(document).ready(function() {
    SafDetails.initTOC();

    $(document).on('mouseenter mouseleave', '.toc-link', function() {
        var $this = $(this);
        var target_id = $this.attr('href').replace('#', '');
        var $target_section = $("[id='" + target_id + "']");

        $target_section.toggleClass('highlight');
    });

    $(document)
        .off('show.bs.collapse', '.collapse')
        .on('show.bs.collapse', '.collapse', function(e) {
            var $this = $(this);

            if ($this.is(e.target)) {
                var $trigger = $(e.target)
                    .siblings('.panel-heading')
                    .find('.panel-switch');

                $trigger
                    .children('i')
                    .removeClass('fa-caret-right')
                    .addClass('fa-caret-down');
            }
        });

    $(document)
        .off('hide.bs.collapse', '.collapse')
        .on('hide.bs.collapse', '.collapse', function(e) {
            var $this = $(this);

            if ($this.is(e.target)) {
                var $trigger = $(e.target)
                    .siblings('.panel-heading')
                    .find('.panel-switch');

                $trigger
                    .children('i')
                    .removeClass('fa-caret-down')
                    .addClass('fa-caret-right');
            }
        });
});

if (document.location.hash.length > 0) {
    $(window).on('scroll.initial-anchor-detection', function() {
        var target_id = window.location.hash.replace('#', '');

        if (target_id.length > 0) {
            target_id = unescape(target_id);

            var $target_section = $("[id='" + target_id + "']");

            if ($target_section.length > 0 && $target_section.offset().top > 0) {
                $(window).scrollTop($target_section.offset().top - 100);
            }
        }

        $(window).off('scroll.initial-anchor-detection');
    });
}

export default SafDetails;
