import { axiosInstance } from 'utils';

var searchPortal = {
    publicCancelMessage: 'By canceling you will lose any unsaved changes.',
    publicSaveMessage: 'You are about to save your current progress.  Once complete you will return to view mode.',
    publicLeavingMessage: 'You are about to navigate away from the current page.  Possibly losing any unsaved changes.',

    _observer: null,
    _privateServlet: 'search',

    setServlet: function(servlet) {
        searchPortal._privateServlet = servlet;
    },

    getServlet: function() {
        return _privateServlet || 'search';
    },

    UTC: function() {
        var today = new Date();
        return today.toISOString();
    },

    /*TODO: is this needed*/
    navigateTo: function(resourceURI) {
        window.location = swa.server + GetServlet() + '?node=' + escape(resourceURI);
    },

    expandField: function(fieldId, buttonId) {
        var $button = $('#' + buttonId);
        var $field = $('#' + fieldId);

        $button.toggleClass('show-more-margin');
        $button
            .children('.show-more-label')
            .toggleClass('show-more-expand')
            .toggleClass('show-more-collapse');

        $field.toggleClass('details-truncate');
    },

    switchToEditForm: function(formId) {
        $('#' + formId + '-actionsButton').css('visibilty', 'hidden');
        $('#' + formId + '-editButton').css('visibilty', 'hidden');

        var params = {
            editing: true,
            viewModeName: '"saf-editing"',
        };

        swa.load(formId + '-loadable', params, function() {
            $('#' + formId + '-editModeButtonBar').css('display', '');

            $('.sticky-save')
                .css('display', '')
                .removeAttr('disabled');

            $('#' + formId)
                .off('change')
                .on('change', function(e) {
                    $('.sticky-save').addClass('save-form-green');
                });

            var target = document.getElementById(formId);

            observer = new MutationObserver(function(mutations) {
                mutations.forEach(function(mutation) {
                    $('.sticky-save').addClass('save-form-green');
                });
            });

            var cfg = {
                childList: true,
                subtree: true,
            };

            observer.observe(target, cfg);

            gadgets.Hub.subscribe('org.topbraid.swa.change', function(event, data) {
                searchPortal.saveDone();
            });
        });
    },

    switchToViewForm: function(formId) {
        var confirm = swa.populateConfirmDialog(publicCancelMessage);

        Promise.resolve(confirm).then(
            function() {
                $('.stick-save')
                    .css('display', 'none')
                    .attr('disabled', 'disabled')
                    .removeClass('save-form-green');

                observer.disconnect();
                observer = null;

                gadgets.Hub.unsubscribe('org.topbraid.swa.change');
                swa.switchToViewForm(formId);
            },
            function(err) {
                return;
            }
        );
    },

    createSimpleResourceHelper: function(
        _base,
        typeURI,
        resourceURI,
        label,
        contextResourceURI,
        handlerURI,
        andThen,
        contextHolderId
    ) {
        var metadata = {
            createdResource: resourceURI,
        };

        var params = {
            metadata: JSON.stringify(metadata),
            resourceType: typeURI,
            uri: '"' + resourceURI + '"',
        };

        if (contextResourceURI) {
            params.contextResource = '<' + contextResourceURI + '>';
        }

        if (label && label.length > 0) {
            params.label = '"' + label + '"^^<http://www.w3.org/2001/XMLSchema#string>';
        }

        params._base = _base || swa.queryGraphURI;
        params._format = 'json';
        params._snippet = true;
        params._viewClass = handleURI ? handleURI : 'saf:CreateResourceSimpleHandler';

        var servletURL = swa.servlet;

        if (contextHolderId) {
            var ctx = $('#' + contextHolderId).attr('swacontext');

            if (ctx && ctx !== '') {
                servletURL += '?' + ctx;
            }
        }

        return axiosInstance
            .get(servletURL, params)
            .then((response) => {
                let data = response.data;
                if (data.error) {
                    swa.populateModalDialog('Operation failed: ' + data.error, 'Operation Failed', null);
                } else if (data.errors || data.warnings) {
                    var msg = 'Constraint violations';
                    if (data.errors) {
                        $.each(data.errors, function(index, item) {
                            msg += '\n - ' + item.message;
                        });
                    }

                    if (data.warnings) {
                        $.each(data.warnings, function(index, item) {
                            msg += '\n - ' + item.message;
                        });
                    }

                    swa.populateModalDialog(msg, null, null);
                }

                if (andThen) {
                    andThen(resourceURI, data);
                }
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    submitForm: function(formId, servlet, onSuccess) {
        //      var nested_docs = $('[id^="nested-create-resource-"]');
        var pre_req_error = false;

        var promises = [];

        var $value = null;

        var confirm = swa.populateConfirmDialog(publicSaveMessage);

        Promise.resolve(confirm).then(
            function() {
                var valid = $('#' + formId)[0].checkValidity();

                if (!valid) {
                    swa.populateModalDialog('Please correct the errors in the submitted form.', null, null);
                    return;
                }

                var time = $('[name^="new-time-"]');

                time.val(UTC());

                if (promises.length > 0) {
                    $.when.apply($, promises).done(function() {
                        searchPortal.submitFullForm(formId, servlet, onSuccess);
                    });
                } else {
                    searchPortal.submitFullForm(formId, servlet, onSuccess);
                }
            },
            function(err) {
                return;
            }
        );
    },

    submitFullForm: function(formId, servlet, onSuccess) {
        if (swa.formHasDirtyFields(formId)) {
            swa.populateModalDialog('Please correct errors in the edit form.', null, null);
        } else {
            var params = $('#' + formId + " :input[class!='no-submit']").serialize();

            if (swa.logMessageBoxStatus) {
                swa.promptLogMessage(params, onSuccess, servlet, formId);
            } else {
                swa.submitEdits(params, onSuccess, servlet, formId);
            }
        }
    },

    saveDone: function() {
        $('.sticky-save')
            .css('display', 'none')
            .attr('disabled', 'disabled')
            .removeClass('save-form-green');

        if ($('#quick_info_loadable').length > 0) {
            swa.load('quick_info_loadable');
        }

        if ($('#update_info_loadable').length > 0) {
            swa.load('quick_info_loadable');
        }
    },

    addEditorRow: function(queryGraphURI, bodyId, single, viewClass, predicateURI, params, appName, inputId) {
        params = params || {};

        params._base = queryGraphURI;
        params._snippet = true;
        params._viewClass = viewClass;
        params.predicate = '<' + predicateURI + '>';

        if (appName && appName !== '') {
            params._contextswaAppName = '"' + appName + '"';
        }

        return axiosInstance
            .get(swa.server + swa.servlet, params)
            .then((response) => {
                var status = 200;

                if (!isNaN(jqXHR.status)) {
                    status = parseInt(jqXHR.status);
                }

                if (status === 202) {
                    if (inputId) {
                        swa.populateModalDialog(
                            'The entered resource (' + $('#' + inputId).val() + ') is already assigned.'
                        );
                        $('#' + inputId)
                            .val('')
                            .trigger('keyup');
                    } else {
                        swa.populateModalDialog('The entered resource is already assigned.');
                    }
                } else {
                    $('#' + bodyId).append(response.data);

                    if (inputId) {
                        $('#' + inputId)
                            .val('')
                            .trigger('keyup');
                    }

                    if (single) {
                        var $formGroup = $('#' + bodyId)
                            .parents('.form-group')
                            .prev('.form-group');

                        $formGroup.find('.saf-add-button').each(function(i, item) {
                            $(item).addClass('swa-display-none');
                        });
                    }
                }
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    },

    addObjectEditorRow: function(queryGraphURI, bodyId, single, subjectURI, predicateURI, appName, editWidgetURI) {
        var params = {
            subject: '<' + subjectURI + '>',
        };

        if (editWidgetURI) {
            params._contexteditWidget = '<' + editWidgetURI + '>';
        }

        searchPortal.addEditorRow(queryGraphURI, bodyId, single, 'saf:ObjectEditorRow', predicateURI, params, appName);
    },

    addTagEditorRow: function(queryGraphURI, bodyId, single, subjectURI, predicateURI, appName, editWidgetURI) {
        var hiddenInput = $('#' + inputId);
        var inputField = $('#' + inputId + '-field');
        var params = {};
        var input;

        if (hiddenInput.val()) {
            input = hiddenInput;
            params.object = hiddenInput.val();
        } else {
            input = inputField;
            params.input = inputField.value();
        }

        if (input.val() !== null && input.val().length > 0) {
            params.subject = toURI(subjectURI);

            if (editWidgetURI) {
                params._contexteditWidget = '<' + editWidgetURI + '>';
            }

            searchPortal.addEditorRow(
                queryGraphURI,
                bodyId,
                single,
                'saf:TagEditorRow',
                predicateURI,
                params,
                appName,
                inputField.attr('id')
            );
        } else {
            swa.populateModalDialog('Field cannot be null.', 'Error', null);
        }
    },

    deleteRow: function(id, targetResourceId, isNested) {
        var row = $('#' + id);

        if (targetResourceId !== undefined && targetResourceId !== null && targetResourceId.length > 0) {
            var $targetResource = $('[name="' + targetResourceId + '"]');

            if ($targetResource.length > 0) {
                $targetResource.remove();
            }
        }

        var formGroup = row.parents('.form-group').prev('.form-group');
        formGroup.find('.saf-add-button').each(function(index, item) {
            $(item).removeClass('swa-display-none');
        });

        if (row.length > 0) {
            row.remove();
        }
    },

    /* TODO:determine if this is needed */
    initTabs: function(tabContainer) {
        $('#' + tabContainer + ' a').tab();
        $('#' + tabContainer + ' a:first').tab('show');
        $('#' + tabContainer + ' a')
            .off('click')
            .on('click', function(e) {
                e.preventDefault();
                $(this).tab('show');
            });
    },

    toURI: function(resource) {
        var returnVal = '';

        if (resource) {
            if (resource.charAt(0) != '<') {
                returnVal = '<' + resource;
            } else {
                returnVal = resource;
            }

            if (resource.charAt(resource.length - 1) != '>') {
                returnVal = returnVal + '>';
            }

            return returnVal;
        }

        return null;
    },

    initExpand: function(btnExpandId, btnCollapseId, tagetClass) {
        var expandBtn = $('#' + btnExpandId);
        var collapsedBtn = $('#' + btnCollapsedId);

        expandBtn.off('click').on('click', function(e) {
            e.preventDefault();
            $('.' + targetClass).collapse('show');
        });

        collapsedBtn.off('click').on('click', function(e) {
            e.preventDefault();
            $('.' + targetClass).collapsed('hide');
        });
    },

    escapeText: function(id) {
        var text = $('#' + id).val();
        if (text.length > 0) {
            if (text.charAt(0) !== /&quot;/) {
                text = '"' + text;
            }

            if (text.charAt(text.length) !== /&quot;/) {
                text = text + '"';
            }

            $('#' + id).val(text);
        }
    },

    /* MH 02_01_2017 */
    enableResultsBar: function(id) {
        $('#' + id + '-nextPage').css('visibility', 'visible');
        $('#' + id + '-previousPage').css('visibility', 'visible');
    },

    disableResultsBar: function(id) {
        $('#' + id + '-count').text('0');
        $('#' + id + '-startIndex').text('0');
        $('#' + id + '-endIndex').text('0');
        $('#' + id + '-nextPage').css('visibility', 'hidden');
        $('#' + id + '-previousPage').css('visibility', 'hidden');
    },

    needsMore: function(id, bid) {
        var $item = $('[id="' + id + '"]');
        var $showMore = $('[id="' + bid + '"]');
        var $content = $item.children('.saf-expandable-content');

        if ($item && $content) {
            if ($content.height() <= $item.height()) {
                $showMore.hide();
            }
        }
    },

    launchNeighborGram: function(resourceURI, base) {
        window.open(
            swa.servlet +
                '?_base=' +
                base +
                '&_viewClass=teamwork:NeighborGramPage' +
                '&focusNode=' +
                escape(resourceURI)
        );
    },

    launchLineageGram: function(resourceURI, base) {
        window.open(
            swa.servlet +
                '?_base=' +
                base +
                '&_viewClass=edg:LineageGramPage' +
                '&focusNode=' +
                escape(resourceURI) +
                '&graph=' +
                base +
                '&viewFramesGraph=http://edg.topbraidlive.org/1.0/config/diagrams'
        );
    },

    launchDownstreamDependencyDiagram: function(resourceURI, base) {
        window.open(
            swa.servlet +
                '?_base=' +
                base +
                '&_viewClass=edg:DownstreamTraceabilityPage' +
                '&focusNode=' +
                escape(resourceURI) +
                '&graph=' +
                base +
                '&viewFramesGraph=http://edg.topbraidlive.org/1.0/config/diagrams'
        );
    },

    launchUsageDiagram: function(resourceURI, base) {
        window.open(
            swa.servlet +
                '?_base=' +
                base +
                '&_viewClass=edg:UsageDiagramPage' +
                '&focusNode=' +
                escape(resourceURI) +
                '&graph=' +
                base +
                '&viewFramesGraph=http://edg.topbraidlive.org/1.0/config/diagrams'
        );
    },
};

$(document).ready(function() {
    $(document).on('click', '.neighborgram-icon-saf', function(e) {
        var $item = $(this),
            itemResource = $item.attr('data-resource'),
            itemGraphUri = $item.attr('data-graph-uri');

        searchPortal.launchNeighborGram(itemResource, itemGraphUri);
    });

    $(document).on('click', '.lineagegram-icon', function() {
        var $item = $(this),
            itemResource = $item.attr('data-resource'),
            itemGraphUri = $item.attr('data-graph-uri');

        searchPortal.launchLineageGram(itemResource, itemGraphUri);
    });

    $(document).on('click', '.downstream-dependency-icon', function() {
        var $item = $(this),
            itemResource = $item.attr('data-resource'),
            itemGraphUri = $item.attr('data-graph-uri');

        searchPortal.launchDownstreamDependencyDiagram(itemResource, itemGraphUri);
    });

    $(document).on('click', '.usage-icon', function() {
        var $item = $(this),
            itemResource = $item.attr('data-resource'),
            itemGraphUri = $item.attr('data-graph-uri');

        searchPortal.launchUsageDiagram(itemResource, itemGraphUri);
    });
});

export default searchPortal;
