var SafSearch = {
    // (function($, swa, searchPortal) {

    clickableHandler: function(link, itemIRI, itemType) {
        var newTerm = {};
        newTerm[itemIRI] = itemType;

        var escaped = encodeURIComponent(itemType + ' ' + itemIRI);

        var url = link + '?ct=' + escaped;

        window.open(url, '_blank');
    },

    load: function(itemType, itemIRI) {
        sessionStorage.setItem('keep_filter', JSON.stringify(true));
        var filterState = searchPortal.Facets.addFacet(itemType, itemIRI);
        searchPortal.Facets.setFilters(filterState);
    },

    loadText: function(inputText, withIndex) {
        sessionStorage.setItem('keep_filter', JSON.stringify(true));
        searchPortal.Facets.addFreeText(decodeURIComponent(inputText), withIndex);
    },

    /**
     * Turns an input element with a given id into a jQuery auto-complete.
     * The actually selected resource will be returned to the server with
     * a hidden field that needs to be passed into this function as well.
     * @param id  the id of the visible input element
     * @param hiddenId  the id of the hidden field
     * @param link  the link for the server callback
     * @param onSelect  an optional handler if selected
     */
    searchAhead: function(id, hiddenId, loadId, link, onSelect) {
        link = swa.redirectLink(link);

        var $element = $('#' + id);

        $element.semanticSearch({
            change: function() {
                if (hiddenId && hiddenId != '') {
                    var text = $('#' + id).val();
                    if (text && text.length == 0) {
                        // Reset hidden field if main field gets emptied
                        $('#' + hiddenId).val('');
                    }
                }
            },
            dataType: 'json',
            minLength: 3,
            delay: 300,
            position: {
                collision: 'flip flip',
            },
            source: function(request, response) {
                //cant update to axios, jquery plugin requirement
                $.post(link, request, response);
            },
            select: function(event, ui) {
                $('#' + id).data('selected', true);

                if (hiddenId && hiddenId != '') {
                    $('#' + hiddenId).val('<' + ui.item.resource + '>');
                    var label = ui.item.editLabel ? ui.item.editLabel : ui.item.label;
                    $('#' + hiddenId).attr('saf-type', label);
                    $('#' + id).removeClass('swa-auto-complete-dirty');
                }

                if (onSelect) {
                    // become source and target params when merging nodes
                    var resource = ui.item.resource;
                    var label = ui.item.label;

                    onSelect.apply(null, [event, hiddenId, loadId]);
                }

                $('#' + hiddenId).val('');
                $('#' + id + '-field').val('');
                event.preventDefault();
            },
            open: function() {
                var $autoCompleteDropdown = $('.ui-autocomplete.ui-front'),
                    $dialog = $('.ui-dialog:visible, .modal:visible');

                // if there is a dialog open, ensure the autocomplete dropdown z-index is heigher than the dialog box
                // otherwise just ensure z-index is set higher than .ui-layout-resizer (2) from jQuery layout
                if ($dialog.length > 0) {
                    $autoCompleteDropdown.css('z-index', parseInt($dialog.css('z-index')) + 1);
                } else {
                    $autoCompleteDropdown.css('z-index', 3);
                }
            },
        });

        $('#' + id + '-drop-down').click(function() {
            if ($($('#' + id).autocomplete('widget')).is(':visible')) {
                $('#' + id).autocomplete('close');
            } else {
                $('#' + id).autocomplete('search');
                $('#' + id).focus();
            }
        });

        $element.on('keydown', function(e) {
            if (e.keyCode === 13) {
                var selected = $('#' + id).data('selected');

                if (!selected) {
                    var $hidden = $('#' + hiddenId);
                    var hiddenVal = $hidden.val();
                    if ($hidden && $hidden.length > 0 && hiddenVal && hiddenVal.length > 0) {
                        e.preventDefault();
                        return;
                    }

                    $('#' + id + '-button').click();
                } else {
                    $('#' + id).data('selected', false);
                }

                $('#' + id).semanticSearch('close');
            }
        });
    },

    selectSuggestion: function(event, inputId, loadId) {
        var $hidden = $('#' + inputId);
        var resource = $hidden.val();
        var type = $hidden.attr('saf-type');

        if (searchPortal.Facets && searchPortal.Facets.select) {
            searchPortal.Facets.select(searchPortal.toURI(type), searchPortal.toURI(resource), loadId);
        }

        $('#' + inputId + '-field').val('');
    },
};

$.widget('custom.semanticSearch', $.ui.autocomplete, {
    _create: function() {
        this._super();

        this._off(this.menu.element, 'menufocus');
        this._on(this.menu.element, {
            menufocus: function(event, ui) {
                var label, item;
                // support: Firefox
                // Prevent accidental activation of menu items in Firefox (#7024
                // #9118)
                if (this.isNewMenu) {
                    this.isNewMenu = false;
                    if (event.originalEvent && /^mouse/.test(event.originalEvent.type)) {
                        this.menu.blur();

                        this.document.one('mousemove', function() {
                            $(event.target).trigger(event.originalEvent);
                        });

                        return;
                    }
                }

                item = ui.item.data('ui-autocomplete-item');

                // Announce the value in the liveRegion
                label = ui.item.attr('aria-label') || item.value;
                if (label && $.trim(label).length) {
                    this.liveRegion.children().hide();
                    $('<div>')
                        .text(label)
                        .appendTo(this.liveRegion);
                }
            },
        });
    },
});

(function($) {
    $(document).ready(function() {
        var pageType = $('body').attr('data-page-type');

        if (pageType === 'search-portal-result-page') {
            //TODO: sessionStorage needs refactored
            sessionStorage.removeItem('free_text');
            sessionStorage.removeItem('facet_state');
            sessionStorage.removeItem('filter_state');
        }
    });
});

export default SafSearch;
