//var searchPortal = searchPortal || {};
import searchPortal from './saf.js';

searchPortal.Facets = searchPortal.Facets = {};

searchPortal.Facets.stateCache = null;

searchPortal.Facets.getState = function() {
    if (searchPortal.Facets.stateCache) {
        return searchPortal.Facets.stateCache;
    }

    var facet_state = sessionStorage.getItem('facet_state');

    if (facet_state === undefined || facet_state === null) {
        facet_state = {};
    } else {
        facet_state = JSON.parse(facet_state);
    }

    searchPortal.Facets.stateCache = facet_state;

    return facet_state;
};

searchPortal.Facets.setState = function(data) {
    searchPortal.Facets.stateCache = null;
    sessionStorage.setItem('facet_state', JSON.stringify(data));
};

searchPortal.Facets.removeState = function() {
    sessionStorage.removeItem('facet_state');
};

searchPortal.Facets.filterCache = null;

searchPortal.Facets.getFilters = function() {
    if (searchPortal.Facets.filterCache) {
        return searchPortal.Facets.filterCache;
    }

    var filter_state = sessionStorage.getItem('filter_state');

    if (filter_state === undefined || filter_state === null) {
        filter_state = {};
    } else {
        filter_state = JSON.parse(filter_state);
    }

    searchPortal.Facets.filterCache = filter_state;

    return filter_state;
};

searchPortal.Facets.setFilters = function(data) {
    searchPortal.Facets.filterCache = null;
    sessionStorage.setItem('filter_state', JSON.stringify(data));
};

searchPortal.Facets.removeFilterState = function() {
    searchPortal.Facets.filterCache = null;
    sessionStorage.removeItem('filter_state');
};

searchPortal.Facets.getFreeText = function() {
    var free_text = sessionStorage.getItem('free_text');

    if (free_text === undefined || free_text === null) {
        free_text = {};
    } else {
        free_text = JSON.parse(free_text);
    }

    return free_text;
};

searchPortal.Facets.setFreeText = function(data) {
    sessionStorage.setItem('free_text', JSON.stringify(data));
};

searchPortal.Facets.removeFreeText = function() {
    sessionStorage.removeItem('free_text');
};

searchPortal.Facets.init = function(loadId) {
    searchPortal.Facets.rebuildState(loadId);
};

searchPortal.Facets.updateLocation = function(param, value) {
    if (history.pushState) {
        var params = $.deparam(window.location.search.substr(1));

        var newurl = window.location.protocol + '//' + window.location.host + window.location.pathname;
        if (param && value) {
            params[param] = value;
        } else if (param && value === null) {
            delete params[param];
        }

        if (params && !$.isEmptyObject(params)) {
            newurl += '?' + decodeURIComponent($.param(params));
        }

        window.history.replaceState(
            {
                path: newurl,
            },
            '',
            newurl
        );
    }
};

searchPortal.Facets.rebuildState = function(loadId) {
    var $elementContainer = $('#' + loadId);
    var facet_state = searchPortal.Facets.getState();
    var key, body, icon;

    for (key in facet_state) {
        body = $("[name='" + key + "']");
        icon = body.prev('div').find("[id$='-twiddle-icon']");

        if (facet_state[key]) {
            body.show();
            body.removeClass('ui-helper-hidden-accessible');
            icon.addClass('swa-facets-icon-chevron-down');
            icon.removeClass('swa-facets-icon-chevron-up');
        }
    }

    var $elements = $elementContainer.find('.swa-facets-collapse');

    if ($elements && $elements.children().length == 0) {
        $elementContainer.find('.saf-empty-result').show();
    } else {
        $elements.css('visibility', 'visible');
    }
};

searchPortal.Facets.toggleTwiddle = function(twiddleId, facetType, queryGraph) {
    var facet_state = searchPortal.Facets.getState();

    var body = $('#' + twiddleId + '-body');
    var icon = $('#' + twiddleId + '-twiddle-icon');

    if (body.css('display') === 'none') {
        body.css('display', '');
        icon.addClass('swa-facets-icon-chevron-down');
        icon.removeClass('swa-facets-icon-chevron-up');

        if (body.hasClass('ui-helper-hidden-accessible')) {
            body.removeClass('ui-helper-hidden-accessible');
        }

        facet_state[facetType] = true;
    } else {
        body.css('display', 'none');
        icon.addClass('swa-facets-icon-chevron-up');
        icon.removeClass('swa-facets-icon-chevron-down');

        delete facet_state[facetType];
    }

    searchPortal.Facets.setState(facet_state);
};

searchPortal.Facets.hideParent = function(containerId) {
    var $container = $('#' + containerId);

    var $facetContainer = $container.parents('.swa-facets-collapse');

    $container.remove();

    if ($facetContainer && $facetContainer.children().length == 0) {
        $facetContainer.next('.saf-empty-result').show();
    }
};

searchPortal.Facets.freeTextSearch = function(inputId, loadId, withIndex) {
    var $input = $('#' + inputId);

    var filter_state = searchPortal.Facets.getFilters();
    var free_text = searchPortal.Facets.getFreeText();

    if ($input.length > 0 && $input.val().length > 0) {
        searchPortal.Facets.updateFreeText($input.val(), loadId, withIndex);
        $input.val('');
        $input.removeClass('swa-auto-complete-dirty');
    } else {
        searchPortal.Facets.removeFreeText();
    }
};

searchPortal.Facets.updateFreeText = function(value, loadId, withIndex) {
    if (value && value.length > 0) {
        searchPortal.Facets.updateLocation('q', encodeURIComponent(searchPortal.Facets.sanatizeFreeText(value)));

        searchPortal.Facets.addFreeText(value, withIndex);
    } else {
        searchPortal.Facets.removeFreeText();
    }

    searchPortal.Facets.refreshFacets(loadId);
};

searchPortal.Facets.sanatizeFreeText = function(value) {
    if (value && value.length > 0) {
        var free_text = value
            .replace(/\\/g, '')
            .replace(/"/g, '')
            .replace(/'/g, "\\'");

        if (value.charAt(0) === '"' && value.charAt(value.length - 1) === '"') {
            free_text = '"' + free_text + '"';
        }

        return free_text;
    }
    return value;
};

searchPortal.Facets.addFreeText = function(value, withIndex) {
    if (withIndex == null) {
        withIndex = false;
    }

    if (value && value.length > 0) {
        var free_text = searchPortal.Facets.sanatizeFreeText(value);

        if (!withIndex) {
            free_text =
                '\\"' +
                value
                    .replace(/\\/g, '')
                    .replace(/"/g, '')
                    .replace(/'/g, "\\'") +
                '\\"';
        }

        searchPortal.Facets.setFreeText(free_text);
    }
};

searchPortal.Facets.destroyFreeText = function(loadId) {
    searchPortal.Facets.removeFreeText();
    searchPortal.Facets.updateLocation('q', null);
    searchPortal.Facets.refreshFacets(loadId);
};

searchPortal.Facets.destroyAll = function(loadId) {
    searchPortal.Facets.removeFilterState();
    searchPortal.Facets.updateLocation('ct', null);
    searchPortal.Facets.removeFreeText();
    searchPortal.Facets.updateLocation('q', null);
    searchPortal.Facets.refreshFacets(loadId);
    //gadgets.Hub.publish('org.topbraid.saf.search.empty');
};

searchPortal.Facets.isEmpty = function() {
    var filterState = searchPortal.Facets.getFilters();

    if (filterState && !$.isEmptyObject(filterState)) {
        return false;
    }

    return true;
};

searchPortal.Facets.addFacet = function(facetType, facetIRI) {
    var filterState = searchPortal.Facets.getFilters();
    facetIRI = facetIRI.replace(/'/g, "\\'");
    filterState[facetIRI] = facetType;

    return filterState;
};

searchPortal.Facets.select = function(facetType, facetIRI, loadId) {
    var filterState = searchPortal.Facets.addFacet(facetType, facetIRI);

    searchPortal.Facets.setFilters(filterState);

    searchPortal.Facets.refreshFacets(loadId);
};

searchPortal.Facets.remove = function(facetType, facetIRI, loadId, emptyId) {
    var filterState = searchPortal.Facets.getFilters();
    facetIRI = facetIRI.replace(/'/g, "\\'");
    delete filterState[facetIRI];

    searchPortal.Facets.setFilters(filterState);

    searchPortal.Facets.refreshFacets(loadId);

    if (searchPortal.Facets.isEmpty()) {
        searchPortal.Facets.updateLocation('ct', null);
    }
};

searchPortal.Facets.buildPayload = function() {
    var key,
        filters = [],
        params = {};
    var filterState = searchPortal.Facets.getFilters();
    var freeText = searchPortal.Facets.getFreeText();

    for (key in filterState) {
        filters.push(filterState[key] + ' ' + key);
    }

    if (filters.length > 0) {
        params.facetFilters = "'" + filters.join('^|') + "'";
    } else {
        params.facetFilters = '"' + '' + '"';
    }

    if (freeText.length > 0) {
        params.freeText = "'" + freeText + "'";
    } else {
        params.freeText = '"' + '' + '"';
    }

    return params;
};

searchPortal.Facets.reloadSearchResults = function(id, startIndex) {
    var _baseParams = {
        pageSize: swa.getSummarySearchPageSize(id),
        startIndex: startIndex,
    };

    var params = searchPortal.Facets.buildPayload();

    var _mergedParams = $.extend({}, _baseParams, params);

    swa.load(id, _mergedParams);
};

searchPortal.Facets.refreshFacets = function(loadId) {
    if (!loadId) {
        return;
    }

    var params = searchPortal.Facets.buildPayload();

    swa.load(loadId, params, function(e) {
        searchPortal.Facets.rebuildState(loadId);
        gadgets.Hub.publish('org.topbraid.saf.search');
    });
};

/* TODO: move this function so it is more specific to the search view */
(function($) {
    $(document).ready(function() {
        var pageType = $('body').attr('data-page-type');

        if (pageType === 'search-portal-page') {
            var keep = false,
                keep_filter = sessionStorage.getItem('keep_filter');

            if (keep_filter) {
                keep = JSON.parse(keep_filter);
                sessionStorage.removeItem('keep_filter');
            }

            if (keep) {
                var $container = $('[id$="-saf-search"]');
                var container_id = $container.attr('id');

                if (container_id && container_id.length > 0) {
                    searchPortal.Facets.filterCache = null;
                    //searchPortal.Facets.refreshFacets(container_id);
                }
            } else {
                searchPortal.Facets.removeFreeText();
                searchPortal.Facets.removeFilterState();
            }

            $(window)
                .off('beforeunload')
                .on('beforeunload', function() {
                    searchPortal.Facets.removeFilterState();
                    searchPortal.Facets.removeFreeText();
                });
        } else if (pageType === 'search-portal-result-page') {
            searchPortal.Facets.removeFreeText();
            searchPortal.Facets.removeFilterState();
        }
    });
})(jQuery);

export default searchPortal;
